










































import { defineComponent } from "@vue/composition-api";

export default defineComponent({
  name: "Referrers",

  props: {
    items: {
      type: Array,
      required: true
    }
  },

  setup() {
    function handleBackButton() {
      this.$emit("on-back-button");
    }

    return {
      handleBackButton
    };
  }
});
